import React, { useState } from 'react';
import './FriendShip.css';

const friendshipImages = [
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/9.jpg' },
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/8.jpg' },
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/7.jpg' },
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/5.png' },
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/3.jpg' },
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/2.jpg' },
  { year: '2024', src: '/ActivityDetail-img/2024/FriendShip/1.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/15.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/14.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/13.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/12.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/11.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/10.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/9.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/8.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/7.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/6.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/5.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/3.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/2.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/2.jpg' },
  { year: '2023', src: '/ActivityDetail-img/2023/FriendShip/1.jpg' },
];

function FriendShip({ selectedYear }) {
  const [selectedImg, setSelectedImg] = useState(null);

  const handleImgClick = (img) => {
    setSelectedImg(img);
  };

  const closeModal = () => {
    setSelectedImg(null);
  };

  const filteredImages = friendshipImages.filter((image) => image.year === selectedYear);

  return (
    <>
      <div className="FriendShip-container">
        {filteredImages.map((image, index) => (
          <img
            onClick={() => handleImgClick(image.src)}
            className="FriendShip-img"
            src={process.env.PUBLIC_URL + image.src}
            key={index}
            alt=""
          ></img>
        ))}

        {/*       */}
        <div className="blank-img"></div>
        <div className="blank-img"></div>
      </div>

      {selectedImg && (
        <div className="img__modal--container" onClick={closeModal}>
          <img src={process.env.PUBLIC_URL + selectedImg} className="img__modal" alt="" />
        </div>
      )}
    </>
  );
}

export default FriendShip;
