import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import './Activity.css';

function Activity() {
  const titles = ['Study', 'Friendship', 'MT'];
  const images = [
    [
      '/ActivityDetail-img/2024/Study/2.jpg',
      '/ActivityDetail-img/2023/Study/2.jpg',
      '/ActivityDetail-img/2023/Study/3.jpg',
    ],
    [
      '/ActivityDetail-img/2023/FriendShip/11.jpg',
      '/ActivityDetail-img/2023/FriendShip/1.jpg',
      '/ActivityDetail-img/2023/FriendShip/10.jpg',
    ],
    [
      '/ActivityDetail-img/2023/MT/3.jpg',
      '/ActivityDetail-img/2023/MT/2.jpg',
      '/ActivityDetail-img/2023/MT/1.jpg',
    ],
  ];

  return (
    <>
      <div className="blank-navbar-pc"></div>
      <div className="blank-navbar-mobile"></div>
      <div className="activity-container">
        <ActivityBlock
          title={titles[0]}
          src1={images[0][0]}
          src2={images[0][1]}
          src3={images[0][2]}
        ></ActivityBlock>
        <ActivityBlock
          title={titles[1]}
          src1={images[1][0]}
          src2={images[1][1]}
          src3={images[1][2]}
        ></ActivityBlock>
        <ActivityBlock
          title={titles[2]}
          src1={images[2][0]}
          src2={images[2][1]}
          src3={images[2][2]}
        ></ActivityBlock>
      </div>
    </>
  );
}

function ActivityBlock(props) {
  return (
    <div className="activity-block">
      <RouterLink to={'/activity-detail'} state={{ activeTab: props.title }}>
        <p className="activity-block__title">{props.title}&nbsp; </p>
      </RouterLink>
      <img
        className="activity-block__image--big"
        src={process.env.PUBLIC_URL + props.src1}
        alt="1"
      ></img>
      <img
        className="activity-block__image--small"
        src={process.env.PUBLIC_URL + props.src2}
        alt="2"
      ></img>
      <img
        className="activity-block__image--small"
        src={process.env.PUBLIC_URL + props.src3}
        alt="3"
      ></img>
    </div>
  );
}

export default Activity;
